.m-article-teaser {
    text-align: left;
    color: $text-color;

    font-size: $base-text-size-mobile;

    p {
        margin: 0 0 1em 0;
    }

    p:last-of-type {
        margin: 0 0;
    }

    > p:first-of-type {
        font-size: 1.25rem;
        font-family: $header-font-family;
        line-height: $header-line-height;
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
    .m-article-teaser {    
        font-size: $base-text-size-desktop;

        > p:first-of-type {
            font-size: 1.875rem;
        }
    }
}